$breakpoint-mobile: 610px;
@media (max-width: $breakpoint-mobile) {
    .main {
        .indexContentWrap{
            width: 100vw;
            height: 100vh;
    
            &>div{

                .cover{
                    width: 80%;
                    height: 200px;
                    position: absolute;
                    top: calc((100vh - 200px)/4);
                    left: 10%;

                
                }
        
                
                .indexLogoDragArea{
                    position: absolute;
                    width: 500px;
                    height: 500px;
                    right: 10%;
                    top: calc((100% - 500px)/2);
                    
                    // background: white;
    
                    .indexLogoDrag {
                        width: 300px;
                        height: 300px;
                        position: absolute;
                        top: calc((100% - 300px)/2);
                        left: calc((100% - 300px)/2);
                        opacity: 0.5;
                        transition: opacity 0.2s;
    
                        &:hover{
                            opacity: 1;
                        }
                    }
                }
    
            }
    
        }
    
        .contentOuterWrap {
            width: calc(100% - 40px);

            &.aboutContentWrap{
                .contentWrap {
                    .content {
                        .textWrap{
                            width: 100%;
                            margin-right: 0;
                        }
                        .skillsWrap {
                            width: 100%;
                        }
                    }
    
                }

            }

            &.contactContentWrap {
                .contactWrap {
                    p{
                        font-size: 16px;
                    }
                } 
            }
        }

    }

}